
.mainp{
    width: 100%;
    height: 100vh;
    background-color: rgb(168, 90, 241);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.centerp{
    width: 35%;
    height: 60vh;
    background-color: rgb(243, 183, 104);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
h2{
    color: black;
    background: transparent;
    font-size: 47px;
    background-color: rgb(228, 49, 79);
    padding: 6px 0 2px 0;
    margin-bottom: 10px;
    width: 100%;
    box-shadow: 5 5 -15 5;
}
.btnp{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
button{
    min-height: 40px;
    border-radius: 5%;
    border-color: transparent;
    background-color:rgb(168, 90, 241) ;
    color: black;
    font-size: 20px;
    border: 2px solid black;
    padding: 0 15px;
    margin-top: 30px;
}
h1{
    font-size: 55px;
    text-align: center;
    display: flex;
    justify-content: first baseline;
}